<template>
  <view class="order-page">
    <view class="page-header" :style="{ paddingTop: statusBarHeight + 10 + 'px' }">
      <text class="page-title">我的订单</text>
    </view>
    
    <view class="order-tabs">
      <view class="tab-item active">
        <text class="tab-text">全部</text>
      </view>
      <view class="tab-item">
        <text class="tab-text">待付款</text>
      </view>
      <view class="tab-item">
        <text class="tab-text">制作中</text>
      </view>
      <view class="tab-item">
        <text class="tab-text">已完成</text>
      </view>
    </view>
    
    <view class="order-list">
      <view class="empty-state">
        <view class="empty-icon">
          <text class="icon">📋</text>
        </view>
        <view class="empty-content">
          <text class="empty-title">暂无订单</text>
          <text class="empty-subtitle">您还没有任何订单记录</text>
        </view>
        <view class="empty-action">
          <text class="action-btn" @tap="goToHome">去拍摄</text>
        </view>
      </view>
    </view>
    
    <!-- 自定义TabBar -->
    <custom-tabbar />
  </view>
</template>

<script>
import CustomTabbar from '@/components/custom-tabbar/custom-tabbar.vue'

export default {
  name: 'OrderPage',
  components: {
    CustomTabbar
  },
  data() {
    return {
      statusBarHeight: 44, // 默认状态栏高度
      headerHeight: 44     // 默认导航栏高度
    }
  },
  methods: {
    goToHome() {
      uni.switchTab({
        url: '/pages/home/home'
      })
    },
    // 获取系统信息，适配刘海屏
    getSystemInfo() {
      try {
        const systemInfo = uni.getSystemInfoSync()
        // 支付宝小程序获取状态栏高度
        this.statusBarHeight = systemInfo.statusBarHeight || 44
        
        // 根据平台和机型设置不同的导航栏高度
        if (systemInfo.platform === 'ios') {
          // iPhone刘海屏适配
          if (systemInfo.statusBarHeight >= 44) {
            this.headerHeight = 44  // iPhone X及以上
          } else {
            this.headerHeight = 44  // iPhone 8及以下
          }
        } else if (systemInfo.platform === 'android') {
          // Android刘海屏适配
          this.headerHeight = 48
        } else {
          this.headerHeight = 44
        }
        
        // 额外的安全区域处理
        const safeArea = systemInfo.safeArea || {}
        const safeAreaTop = safeArea.top || this.statusBarHeight
        
        // 使用更安全的顶部距离
        this.statusBarHeight = Math.max(this.statusBarHeight, safeAreaTop)
        
        console.log('系统信息:', {
          platform: systemInfo.platform,
          statusBarHeight: this.statusBarHeight,
          headerHeight: this.headerHeight,
          model: systemInfo.model,
          safeArea: safeArea
        })
      } catch (e) {
        console.log('获取系统信息失败，使用默认值:', e)
        // 降级方案：根据用户代理判断
        const userAgent = navigator.userAgent || ''
        if (userAgent.includes('iPhone')) {
          this.statusBarHeight = 44
          this.headerHeight = 44
        } else {
          this.statusBarHeight = 44
          this.headerHeight = 48
        }
      }
    }
  },
  onLoad() {
    console.log('订单页加载完成')
    this.getSystemInfo()
  },
  onShow() {
    // 隐藏原生TabBar
    uni.hideTabBar({
      animation: false
    })
  }
}
</script>

<style scoped>
/* 全局CSS变量定义 */
:root {
  --status-bar-height: 44px;
}

/* 支付宝小程序环境变量支持 */
@supports (top: env(safe-area-inset-top)) {
  :root {
    --status-bar-height: env(safe-area-inset-top);
  }
}

/* 微信小程序环境变量支持 */
@supports (top: constant(safe-area-inset-top)) {
  :root {
    --status-bar-height: constant(safe-area-inset-top);
  }
}

.order-page {
  background-color: #f5f5f5;
  min-height: 100vh;
  padding-bottom: 120rpx; /* 为自定义tabbar预留空间 */
}

.page-header {
  background: white;
  padding: 0 20rpx 30rpx;
  text-align: center;
  position: relative;
  /* CSS环境变量回退方案 */
  padding-top: var(--status-bar-height, 44px);
}

/* 针对特殊机型的适配 */
@media screen and (device-width: 414px) and (device-height: 896px) {
  /* iPhone XR, iPhone 11 */
  .page-header {
    padding-top: 44px;
  }
}

@media screen and (device-width: 375px) and (device-height: 812px) {
  /* iPhone X, iPhone XS, iPhone 11 Pro */
  .page-header {
    padding-top: 44px;
  }
}

@media screen and (device-width: 414px) and (device-height: 896px) {
  /* iPhone XS Max, iPhone 11 Pro Max */
  .page-header {
    padding-top: 44px;
  }
}

.page-title {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
}

.order-tabs {
  background: white;
  display: flex;
  padding: 0 20rpx;
  border-bottom: 1rpx solid #e9ecef;
}

.tab-item {
  flex: 1;
  text-align: center;
  padding: 30rpx 0;
  position: relative;
}

.tab-item.active .tab-text {
  color: #3d45e6;
  font-weight: 500;
}

.tab-item.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60rpx;
  height: 4rpx;
  background: #3d45e6;
  border-radius: 2rpx;
}

.tab-text {
  font-size: 28rpx;
  color: #666;
}

.order-list {
  padding: 40rpx 20rpx;
}

.empty-state {
  text-align: center;
  padding: 120rpx 40rpx 80rpx;
  background: white;
  margin: 20rpx;
  border-radius: 20rpx;
  box-shadow: 0 2rpx 20rpx rgba(0, 0, 0, 0.05);
}

.empty-icon {
  margin-bottom: 30rpx;
}

.icon {
  font-size: 120rpx;
  opacity: 0.6;
  filter: grayscale(0.3);
}

.empty-content {
  margin-bottom: 60rpx;
}

.empty-title {
  display: block;
  color: #333;
  font-size: 32rpx;
  font-weight: 500;
  margin-bottom: 16rpx;
  letter-spacing: 1rpx;
}

.empty-subtitle {
  display: block;
  color: #999;
  font-size: 26rpx;
  line-height: 1.5;
}

.empty-action {
  margin-top: 40rpx;
}

.action-btn {
  display: inline-block;
  background: linear-gradient(135deg, #3d45e6 0%, #5b63f5 100%);
  color: white;
  padding: 24rpx 60rpx;
  border-radius: 50rpx;
  font-size: 28rpx;
  font-weight: 500;
  box-shadow: 0 8rpx 30rpx rgba(61, 69, 230, 0.3);
  transition: all 0.3s ease;
}

.action-btn:active {
  transform: translateY(2rpx);
  box-shadow: 0 4rpx 20rpx rgba(61, 69, 230, 0.4);
}
</style>
